@import url(chrome://flashblock/content/flashblock.css);
@font-face {
  font-family: "Staatliches";
  src: url(/semcomp-a11y/static/media/Staatliches-Regular.7794533c.woff),
    url(/semcomp-a11y/static/media/Staatliches-Regular.d5746e3b.ttf),
    url(/semcomp-a11y/static/media/Staatliches-Regular.27b266bf.eot);
}

.shiny-divider {
  /* Divisor */
  width: 100%;
  height: 3px;

  /* Degradê Brilhante */
  background: radial-gradient(
    50% 50% at 50% 50%,
    rgba(255, 255, 255, 1) 40.1%,
    rgba(141, 61, 153, 1) 100%
  );
}
.home-about-background {
  background-color: var(--color-blue-secondary);
}

.home-about-container {
  display: grid;
  align-items: center;
  justify-content: center;
  justify-items: center;
  align-content: center;
  grid-template-columns: 1fr 1fr;
  padding: 5rem 2rem;
  grid-column-gap: 2rem;
  -webkit-column-gap: 2rem;
          column-gap: 2rem;
  max-width: 80rem;
  margin: 0 auto;
  color: white;
}

.home-about-container > .text-container > h2 {
  margin: 0;
  font-size: 2.5rem;
}

.home-about-container > .text-container > p {
  margin: 2rem 0;
}

.home-about-container > .text-container > .about-signup-button {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  background-color: var(--color-blue-main);
  border-radius: 8px;
  padding: 1rem 4rem;
  margin: 0 auto; /* Center button horizontaly */
  font-size: 1.2rem;
  font-weight: bold;
  text-align: center;
}

.home-about-container > .text-container > .about-signup-button:hover {
  text-decoration: none;
}

.home-about-container > .text-container > .about-signup-button-disabled {
  -webkit-filter: contrast(50%);
          filter: contrast(50%);
}

.home-about-container > img {
  border-radius: 8px;
  width: 90%;
}

.big-shiny-divider {
  /* Divisor */
  position: absolute;
  width: 100%;
  height: 6px;
  z-index: 9999;

  /* Degradê Brilhante */
  background: radial-gradient(
    50% 50% at 50% 50%,
    rgba(255, 255, 255, 1) 40.1%,
    rgba(141, 61, 153, 1) 100%
  );
}

/* Tablet */
@media screen and (max-width: 768px) {
  .home-about-container {
    display: flex;
    flex-direction: column-reverse;
  }

  .home-about-container > img {
    margin-bottom: 2rem;
  }
}

.footer-component {
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #1d1d1b;
  padding: 2rem;
  background-image: url(/semcomp-a11y/static/media/footer-background.19b9081b.png);
}

.footer-component > div {
  display: flex;
  margin: 1rem 0;
}

.footer-component > div > a {
  width: 2rem;
  height: 2rem;
  border-radius: 100%;
  border: 1px solid rgba(255, 255, 255, 0.2);
  display: block;
  padding: 0.4rem;
  margin: 0 0.4rem;
}

.footer-component > div > a > img {
  width: 100%;
  height: 100%;
}

.footer-component > p {
  margin: 0;
  font-size: 1.2rem;
}

.footer-component > p.copyright {
  font-size: 0.8rem;
}

