.spinner svg {
  display: inline;
}

.spinner circle {
  -webkit-transform-origin: 50% 50% 0;
          transform-origin: 50% 50% 0;
  stroke-dasharray: 125.6;
}

.spinner circle.extra-small {
  -webkit-animation: spinExtraSmall 3000ms linear Infinite;
          animation: spinExtraSmall 3000ms linear Infinite;
  stroke-dasharray: 37.68;
}
.spinner circle.small {
  -webkit-animation: spinSmall 3000ms linear Infinite;
          animation: spinSmall 3000ms linear Infinite;
  stroke-dasharray: 56.52;
}
.spinner circle.medium {
  -webkit-animation: spinMedium 3000ms linear Infinite;
          animation: spinMedium 3000ms linear Infinite;
  stroke-dasharray: 75.36;
}
.spinner circle.large {
  -webkit-animation: spinLarge 3000ms linear Infinite;
          animation: spinLarge 3000ms linear Infinite;
  stroke-dasharray: 125.6;
}

@-webkit-keyframes spinLarge {
  0% {
    stroke-dashoffset: 26.4;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  50% {
    stroke-dashoffset: 125.6;
    -webkit-transform: rotate(720deg);
            transform: rotate(720deg);
  }
  100% {
    stroke-dashoffset: 26.4;
    -webkit-transform: rotate(1080deg);
            transform: rotate(1080deg);
  }
}

@keyframes spinLarge {
  0% {
    stroke-dashoffset: 26.4;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  50% {
    stroke-dashoffset: 125.6;
    -webkit-transform: rotate(720deg);
            transform: rotate(720deg);
  }
  100% {
    stroke-dashoffset: 26.4;
    -webkit-transform: rotate(1080deg);
            transform: rotate(1080deg);
